import React from "react";
import { Image, Header, Grid, Placeholder, Segment } from "semantic-ui-react";
import CarouselView from "../Components/Carousel"
import Facebook from '../Assets/img/Home/facebook.svg'
import Youtube from '../Assets/img/Home/youtube.svg'
import Blogger from '../Assets/img/Home/blogger.svg'
import Services from "./Services";

const HomeContainer = () => {
  return (
    <div>
      <CarouselView style={{ marginTop: 0, padding: 0 }} />
      <Services/>
      <Header as='h3' textAlign='center'><u>REDES SOCIALES</u></Header>
      <Segment.Group horizontal>
        <Segment inverted color='blue'><Image src={Facebook} fluid /></Segment>
        <Segment inverted color='red'><Image src={Youtube} /></Segment>
        <Segment inverted color='yellow'><Image src={Blogger} fluid /></Segment>
      </Segment.Group>
      <Grid>
        <Grid.Column only='computer'>
          <Placeholder>
            <Placeholder.Line />
            <Placeholder.Line />
            <Placeholder.Line />
            <Placeholder.Line />
            <Placeholder.Line />
          </Placeholder>
        </Grid.Column>
      </Grid>
    </div>
  );
};

export default HomeContainer;
